<template>

  <div class="xdh-map-warp" >
    <div class="map-warp">
      <slot name="map"></slot>
    </div>
    <slot></slot>
  </div> 
  
</template>

<style lang="scss" scoped>
</style>



<script>
  /**
   * warp 
   * @module xdh-map-warp
   */
    
  
   /**
   * 
   * @member slots
   * @property {string} map z-index: 0
   * @property {string} default 
   * 
   */

  export default {
    name: 'xdh-map-warp',
    /**
    * 
    * @member props
    * @property {number} dialogLayer z-index xdh-map-dialogzindex dialogLayer  warp  xdh-map-dialog  z-index
    * 
    */
    props: {
      dialogLayer: {
        type: Number,
        default: 1
      }
    },
    data() {
      return {
        dialogList: []
         
      }
    },
    provide() {
      return {
        dialogLayer: this.dialogLayer
      }
    },
    watch: { 
    },
    methods: {
      registerDialog(vm) {
        this.dialogList = this.dialogList.concat([vm])
      },
      getAllDialogs() {
        return this.dialogList
      },
      setDialogsSeries(vm) {
        let targetIndex = this.dialogList.findIndex((dialog) => {
          return dialog._uid === vm._uid
        })
        let current = this.dialogList.splice(targetIndex, 1)
        this.dialogList = current.concat(this.dialogList)
        return this.dialogList
      }
    },
    mounted() {
 

    },
    beforeDestroy() {
      
    }
  }
</script>

